<template>
    <v-card class="list-sortable mx-auto mb-10 mt-10 rounded-lg" max-width="350" tile>
        <div v-if="'' != todoList">
            <draggable v-model="todoList" handle=".move-btn" group="people" @start="drag=true" @end="updateTodoListOrder">
                <v-list-item class="list-item" v-for="todo in todoList" :key="todo.id">
                    <v-list-item-content>
                        <v-menu offset-y rounded>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    class="todo-menu"
                                    icon
                                    v-bind="attrs"
                                    v-on="on"
                                    color="primary"
                                >
                                    <v-icon>mdi-dots-vertical</v-icon>
                                </v-btn>
                                <div v-if="todo.status">
                                    <v-list-item-subtitle class="mb-2">Completed: {{todo.completed}}</v-list-item-subtitle>
                                </div>
                                <div v-else>
                                    <v-list-item-subtitle class="mb-2">Updated: {{todo.updated}}</v-list-item-subtitle>
                                    <v-list-item-subtitle class="mb-2">Created: {{todo.created}}</v-list-item-subtitle>
                                </div>
                                <v-list-item-title :class="{ completed: todo.status }" class="mb-2 relative">{{todo.text}}</v-list-item-title>
                                <v-btn
                                    class="move-btn"
                                    icon
                                    color="primary"
                                >
                                    <v-icon>mdi-menu</v-icon>
                                </v-btn>
                            </template>
                            <v-list>
                                <v-list-item @click="completeTodo(todo.id)" link v-if="todo.status == false">
                                    <v-list-item-title>Complete</v-list-item-title>
                                </v-list-item>
                                <v-list-item @click="updateTodo(todo.id)" link v-if="todo.status == false">
                                    <v-list-item-title>Update todo</v-list-item-title>
                                </v-list-item>
                                <v-list-item @click="deleteTodo(todo.id)" link>
                                    <v-list-item-title>Delete todo</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                    </v-list-item-content>
                </v-list-item>
            </draggable>
        </div>
        <div v-else class="pa-5">
            No todo found!
        </div>
    </v-card>
</template>

<script>
import eventBus from '../EventBus';
import draggable from 'vuedraggable'
export default {
    name: 'TodoList',
    components: {
        draggable,
    },
    data() {
        return {
            apiUrl: 'http://localhost/projeler/php-symfony/todo-app/api/api.php',
            todoList: []
        }
    },
    methods: {
        getTodoList() {
            this.todoList = [];
            var self = this;
            this.axios.get(this.apiUrl, {
                params: {
                    action: 'getTodoList'
                }
            })
            .then(function (response) {
                if ( '' != Object.keys(response.data) ) {
                    Object.keys(response.data).forEach(function(key) {
                        self.todoList.push(response.data[key]);
                    });
                }
            })
            .catch(function (error) {
                console.log(error);
            });
        },
        completeTodo(todoId) {
            var self = this;
            const params = new URLSearchParams();
            params.append('action', 'complete');
            params.append('id', todoId);
            this.axios.post(this.apiUrl, params)
            .then(function (response) {
                self.getTodoList();
                if ( false == response.data ) {
                    alert("There was a problem update Todo!");
                } else {
                    alert("Mission completed successfully!");
                }
            })
            .catch(function (error) {
                console.log(error);
            });
        },
        updateTodo(todoId) {
            var self = this;
            var newTodo = prompt("What is your new quest content?");
            if (newTodo !== null && newTodo !== "") {
                const params = new URLSearchParams();
                params.append('action', 'update');
                params.append('id', todoId);
                params.append('newTodo', newTodo);
                this.axios.post(this.apiUrl, params)
                .then(function (response) {
                    self.getTodoList();
                    if ( false == response.data ) {
                        alert("There was a problem updated Todo!");
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
            }
        },
        deleteTodo(todoId) {
            var self = this;
            var result = confirm("Are you sure you want to delete it? There is no turning back!");
            if (result === true) {
                const params = new URLSearchParams();
                params.append('action', 'delete');
                params.append('id', todoId);
                this.axios.post(this.apiUrl, params)
                .then(function (response) {
                    self.getTodoList();
                    if ( false == response.data ) {
                        alert("There was a problem delete Todo!");
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
            }
        },
        updateTodoListOrder() {
            const params = new URLSearchParams();
            params.append('action', 'updateListOrder');
            params.append('todoList', JSON.stringify(this.todoList));
            this.axios.post(this.apiUrl, params)
            .then(function (response) {
                if ( false == response.data ) {
                    alert("An unexpected problem has occurred!");
                }
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    },
    created() {
        this.getTodoList();
        eventBus.$on('newTodoAdded', () => {
            this.getTodoList();
        });
    }
}
</script>

<style>
    .todo-menu{
        width: 50px;
        height: 50px;
        position: absolute!important;
        top: 10px!important;
        right:10px!important;
    }
    .move-btn{
        width: 25px;
        height: 25px;
        position: absolute!important;
        top: calc( 50% - 18px )!important;
        left:5px!important;
    }
    .list-item{
        padding-left: 50px!important;
    }
    .completed{
        text-decoration: line-through
    }
</style>