<template>
    <v-app>
        <v-app-bar app color="primary" dark>
        <div class="d-flex align-center">
            TodoList
        </div>

        <v-spacer></v-spacer>
            <v-tooltip left>
            <template v-slot:activator="{ on, attrs }">
                <v-btn @click="addNewTodo()"
                light
                v-bind="attrs"
                v-on="on"
                >
                    <v-icon>mdi-plus</v-icon>
                </v-btn>
            </template>
            <span>Add new todo</span>
            </v-tooltip>
        </v-app-bar>

        <v-main>
            <TodoList />
        </v-main>
    </v-app>
</template>

<script>
import TodoList from './components/TodoList.vue';
import eventBus from './EventBus';

export default {
    name: 'App',
    components: {
        TodoList
    },
    data() {
        return {
            apiUrl: 'http://localhost/projeler/php-symfony/todo-app/api/api.php'
        }
    },
    methods: {
        addNewTodo() {
            var newTodo = prompt("What is your new quest content?");
            if (newTodo !== null && newTodo !== "") {
                const params = new URLSearchParams();
                params.append('action', 'add');
                params.append('newTodo', newTodo);
                this.axios.post(this.apiUrl, params)
                .then(function (response) {
                    eventBus.$emit('newTodoAdded');
                    if ( false == response.data ) {
                        alert("There was a problem updated Todo!");
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
            }
        }
    }
};
</script>